<template>
  <div>
    <h3>food</h3>
    <go-back></go-back>
    <!-- 绑定事件 -->
    <ul>
      <li v-for="item in list" :key="item.id" @click="toDetail(item.id,item.name)">
        <h3>{{item.name}}</h3>
        <div>price:{{item.price|filterPrice}}</div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        { id: 1, name: "麻辣烫", price: 20 },
        { id: 2, name: "麻辣香锅", price: 59.9 },
        { id: 3, name: "麻辣小龙虾", price: 109.9 },
      ],
    };
  },
  methods:{
    toDetail(id,name){
      // 动态路由
      this.$router.push("/foodDetail/"+id+"/"+name)
    }
  }
};
</script>

<style scoped>
li{
  margin:0.2rem;
  border: 0.01rem solid #ccc;
}
</style>